.footer {
  padding: 2.5rem 0;
  color: #fff;
  padding-top: 0rem;
}
.footer .social {
  text-align: center;
  padding-bottom: 1.563rem;
  color: #4b4c4d;
}
.footer .social a {
  border: 1px solid #ccc;
  color: inherit;
  line-height: 2.375rem;
  display: inline-block;
  width: 2.5rem;
  height: 2.5rem;
  text-align: center;
  border-radius: 50%;
  margin: 0 0.5rem;
  opacity: 0.75;
}
.footer .social a:hover {
  opacity: 0.9;
  color: gold;
  border: 1px solid gold;
}
.footer .list {
  margin-top: 0;
  padding: 0;
  list-style: none;
  font-size: 1rem;
  line-height: 1.6;
  text-align: center;
}
.footer .list a {
  color: inherit;
  text-decoration: none;
}

.footer ul li {
  display: inline-block;
  padding: 0 1rem;
}

.footer ul a:hover {
  color: gold;
  text-decoration: none;
  opacity: 0.8;
}

.footer .copyright {
  margin: 1rem;
  text-align: center;
  font-size: 0.9rem;
  color: white;
  opacity: 0.8;
}
